// Custom styles
.header {
	&-wrapper {
		padding: $gutter-width;
		display: flex;
	}
	&-logo {
		display: block;
		line-height: 1;
		img {
			height: 56px;
			width: auto;
		}
	}
	&-user {
		margin: 0 0 0 auto;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		&-content {
			text-align: right;
			line-height: 1;
			h2 {
				font-size: 16px;
				margin: 0;
				line-height: 1;
			}
			small {
				font-size: 10px;
			}
		}
		&-avatar {
			width: 40px;
			height: 40px;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 26px;
			text-align: center;
			color: white;
			line-height: 1;
			text-transform: uppercase;
			margin: 0 0 0 1rem;
			font-weight: 500;
			background: color("primary", "base");
		}
	}
}